import React from 'react';
import { graphql } from 'gatsby';
import TagPage from 'containers/TagPage';

const TagTemplate = (props: any) => {
  return <TagPage {...props} />;
};

export default TagTemplate;

export const pageQuery = graphql`
  query TagQuery($skip: Int!, $limit: Int!, $tag: String!, $lang: String!) {
    allMarkdownRemark(
      skip: $skip
      limit: $limit
      sort: { fields: [frontmatter___date], order: DESC }
      filter: { frontmatter: { tags: { in: [$tag] } }, fields: { lang: { eq: $lang } } }
    ) {
      edges {
        node {
          ...PostQueryFragment
        }
      }
      pageInfo {
        currentPage
        pageCount
      }
    }
  }
`;
